export default (sequelize, Sequelize) => {
  const SubjectTypeNew = sequelize.define("subjectTypeNew", {
    name_th: {
      type: Sequelize.STRING,
    },
    name_en: {
      type: Sequelize.STRING,
    },
    description_th: {
      type: Sequelize.STRING,
    },
    description_en: {
      type: Sequelize.STRING,
    },
  });
  return SubjectTypeNew;
};
